<template>
	<div class="course clear">
		<!-- 搜索工具栏start -->
		<div class="search clear pd20">
			<div class="search_item">创建时间：
				<el-date-picker  v-model="search.timeRange"  type="datetimerange"  range-separator="至"  start-placeholder="开始日期"  end-placeholder="结束日期" value-format="timestamp"></el-date-picker>
			</div>
			<div class="search_item">课程名称：
				<el-input placeholder="请输入内容" v-model="search.courseTitle" clearable style="width:300px"></el-input>
			</div>
			<div class="search_item">所属产品：
				<el-select v-model="search.product_id" placeholder="请选择" style="width:200px">
					<el-option v-for="item in productOptions" :key="item.value" :label="item.text" :value="item.value"></el-option>
				</el-select>
			</div>
			<div class="search_item">发布状态：
				<el-select v-model="search.status" placeholder="请选择" style="width:200px">
					<el-option v-for="item in statusOptions" :key="item.value" :label="item.text" :value="item.value"></el-option>
				</el-select>
			</div>
			<div class="search_item">公开状态：
				<el-select v-model="search.is_public" placeholder="请选择" style="width:200px">
					<el-option v-for="item in publicOptions" :key="item.value" :label="item.text" :value="item.value"></el-option>
				</el-select>
			</div>
			<el-button type="primary" icon="el-icon-search" class="search_btn" @click="searchCourse">搜索</el-button>
		</div>
		<!-- 搜索工具栏end -->

		<!-- 操作工具栏start -->
		<div class="tools clear pd20">
			<el-button type="primary" icon="el-icon-plus" size="mini" @click="addCourse">创建</el-button>
			<el-button type="danger" icon="el-icon-delete" size="mini" @click="delCourse" v-if="multipleSelection.length > 0">删除</el-button>
			<el-button type="primary" icon="el-icon-check" size="mini" @click="setCourseStatus(true)"  v-if="multipleSelection.length > 0">发布</el-button>
			<el-button type="danger" icon="el-icon-close" size="mini" @click="setCourseStatus(false)"  v-if="multipleSelection.length > 0">停止发布</el-button>
		</div>
		<!-- 操作工具栏end -->

		<!-- 课程列表start -->
		<el-table ref="multipleTable" :data="courseDatas" tooltip-effect="dark" style="width: 100%;min-height: 630px;" border
			@cell-click="cellClick"  @selection-change="selectionChange">
				<el-table-column type="selection" width="50" align="center"></el-table-column>
				<el-table-column label="课程ID" width="100" prop="id" align="center"></el-table-column>
				<el-table-column label="所属产品" width="140" prop="product_title" align="center" 
					:filters="productOptions" :filter-method="filterProductTitle" filter-placement="bottom-end">
				</el-table-column>
				<el-table-column label="课程名称" width="400" align="center" show-overflow-tooltip>
					<template slot-scope="scope"><span style="color:#409EFF">{{ scope.row.title}}</span></template>
				</el-table-column>
				<el-table-column label="发布状态" width="100" align="center" prop="status"
					:filters="statusOptions" :filter-method="filterStatus" filter-placement="bottom-end">
					<template slot-scope="scope">{{ scope.row.status === 1 ? '已发布' :'未发布'}}</template>	
				</el-table-column>
				<el-table-column label="公开状态" width="100" align="center" prop="is_public"
					:filters="publicOptions" :filter-method="filterPublic" filter-placement="bottom-end">
					<template slot-scope="scope">{{ scope.row.is_public === 1 ? '公开' :'不公开'}}</template>	
				</el-table-column>
				<el-table-column label="创建时间" width="200" align="center">
					<template slot-scope="scope">{{ formatDateTime(scope.row.created_time)}}</template>	
				</el-table-column>
				<el-table-column label="操作" align="center" fixed="right">
					<template slot-scope="scope">
						<el-button @click.native.prevent="openCourseDialog(scope.$index, courseDatas)" type="text" size="small">修改</el-button>
					</template>
				</el-table-column>
		</el-table>
		<el-pagination  @size-change="handleSizeChange"  @current-change="handleCurrentChange"  :current-page="pageConfig.pageIndex" class="pd20"
      		:page-sizes="[10, 20, 50, 100]" :page-size="pageConfig.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="pageConfig.pageTotal">
		</el-pagination>
		<!-- 课程列表end -->

		<!-- 查看课程信息start -->
		<el-dialog :visible.sync="dialogVisibleShow" :fullscreen="true" class="dialog">
			<div class="dialog_section">
				<h3>基本信息</h3>
				<div class="dialog_list">
					<div class="section_item">
						<div class="section_item_name">所属名称</div>
						<div class="section_item_content">{{ courseDetail.product_title }}</div>
					</div>
					<div class="section_item section_item_desc">
						<div class="section_item_name" :style="{'height':course_desc_height}">课程简介</div>
						<div class="section_item_content" id="course_desc" ref="course_desc">{{ courseDetail.introduction }}</div>
					</div>
					<div class="section_item">
						<div class="section_item_name">封面地址</div>
						<div class="section_item_content" style="color:blue">{{ courseDetail.cover }}</div>
					</div>
					<div class="section_item">
						<div class="section_item_name">公开状态</div>
						<div class="section_item_content" style="color:red">
							<span v-for="(item,index) in publicOptions" v-if="item.value == courseDetail.is_public" :key="index">{{ item.text }}</span>
						</div>
					</div>
					<div class="section_item">
						<div class="section_item_name">发布状态</div>
						<div class="section_item_content" style="color:red">
							<span v-for="(item,index) in statusOptions" v-if="item.value == courseDetail.status" :key="index">{{ item.text }}</span>
						</div>
					</div>
				</div>
			</div>
			<div class="dialog_section" v-for="(item,index) in courseDetail.chapters" :key="index">
				<h3>课程详情：第{{index+1}}节</h3>
				<div class="dialog_list" >
					<div class="section_item">
						<div class="section_item_name">课程标题</div>
						<div class="section_item_content" style="color:red">{{item.title}}</div>
					</div>
					<div class="section_item">
						<div class="section_item_name">视频链接</div>
						<div class="section_item_content" style="color:blue">{{item.video_link}}</div>
					</div>
				</div>
			</div>
		</el-dialog>
		<!-- 查看课程信息end -->

		<!-- 编辑/创建课程信息start -->
		<el-dialog :visible.sync="dialogVisibleEdit" :fullscreen="true" class="dialog editDialog">
			<el-form :model="courseDetail" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
				<div class="dialog_section">
					<h3>基本信息</h3>
					<div class="editDialogBtn">
						<el-button type="success" @click="submitForm('ruleForm')" size="mini" icon="el-icon-check">保存</el-button>
						<el-button @click="dialogVisibleEdit = false" size="mini" icon="el-icon-back">返回</el-button>
					</div>
					<el-form-item label="所属名称" prop="product_title">
						<el-select v-model="courseDetail.product_title" placeholder="请选择活动区域">
							<el-option v-for="(item,index) in productOptions" v-if="item.value != 0" :key="index" :label="item.text" :value="item.text"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="课程名称" prop="title">
						<el-input v-model="courseDetail.title" placeholder="请输入课程名称"></el-input>
					</el-form-item>
					<el-form-item label="课程简介" prop="introduction">
						<el-input type="textarea" v-model="courseDetail.introduction" :autosize="{ minRows: 2, maxRows: 4}" placeholder="请输入课程简介"></el-input>
					</el-form-item>
					<el-form-item label="封面地址" prop="cover">
						<el-input v-model="courseDetail.cover" placeholder="请输入封面地址"></el-input>
					</el-form-item>
					<el-form-item label="公开状态" prop="is_public">
						<el-radio-group v-model="courseDetail.is_public">
							<el-radio v-for="(item,index) in publicOptions"  v-if="item.value != -1" :key="index" :label="item.value">{{item.text}}</el-radio>
						</el-radio-group>
					</el-form-item>
					<el-form-item label="发布状态" prop="status">
						<el-radio-group v-model="courseDetail.status">
							<el-radio v-for="(item,index) in statusOptions"  v-if="item.value != -1" :key="index" :label="item.value">{{item.text}}</el-radio>
						</el-radio-group>
					</el-form-item>
				</div>
				<div v-for="(item, index) in courseDetail.chapters" :key="index" class="dialog_section" v-if="item.status">
					<h3>课程详情：第{{index+1}}节</h3>
					<div class="editDialogBtn">
						<el-button type="primary" size="mini" icon="el-icon-sort-up" v-if="index != 0"  @click.prevent="moveChapter(item,'up')">上移</el-button>
						<el-button type="primary" size="mini" icon="el-icon-sort-down" v-if="index != courseDetail.chapters.length-1" @click.prevent="moveChapter(item,'down')">下移</el-button>
						<el-button type="warning" icon="el-icon-delete" size="mini" @click.prevent="removeChapter(item)">删除</el-button>
					</div>
					<el-form-item label="课节标题" :prop="'chapters.' + index + '.title'"
						:rules="[{ required: true, message: '请输入课节标题', trigger: 'blur' },{ min: 1, max: 100, message: '长度在 1 到 100 个字符', trigger: 'blur' }]">
						<el-input v-model="item.title"></el-input>
					</el-form-item>
					<el-form-item label="视频链接" :prop="'chapters.' + index + '.video_link'"
						:rules="[{ required: true, message: '请输入视频链接', trigger: 'blur' },{ min: 1, max: 100, message: '长度在 1 到 100 个字符', trigger: 'blur' }]">
						<el-input v-model="item.video_link"></el-input>
					</el-form-item>
				</div>
			</el-form>
			<div style="text-align: center;margin-bottom:100px">
				<el-button type="primary" icon="el-icon-plus" size="mini" @click="addChapter">添加</el-button>
			</div>
		</el-dialog>
		<!-- 编辑/创建课程信息end -->
	</div>
</template>
<script>
export default {
	data() {
		return {
			search:{
				timeRange:[],
				courseTitle:'',
				is_public:-1,
				status:-1,
				product_id:0
			},
			productOptions : [],
			statusOptions:[{ text: '全部', value: -1 },{ text: '已发布', value: 1 }, { text: '未发布', value: 0 }],
			publicOptions:[{ text: '全部', value: -1 },{ text: '公开', value: 1 }, { text: '不公开', value: 0 }],
			courseDatas: [],
			multipleSelection: [],
			courseDetail:{},
			courseDetailBefore:{},
			dialogVisibleShow:false,
			dialogVisibleEdit:false,
			rules: {
				product_title: [{required: true, message: '请选择所属名称', trigger: 'change' }],
				title: [{ required: true, message: '请输入课程名称', trigger: 'blur' },{ min: 1, max: 100, message: '长度在 1 到 100 个字符', trigger: 'blur' }],
				introduction: [{ required: true, message: '请填写课程简介', trigger: 'blur' }],
				cover: [{ required: true, message: '请填写封面URL地址', trigger: 'blur' }],
				is_public: [{ required: true, message: '请选择公开状态', trigger: 'change' }],
				status: [{ required: true, message: '请选择发布状态', trigger: 'change' }],
			},
			course_desc_height:'',
			pageConfig:{
				pageIndex:1,
				pageTotal:0,
				pageSize:10,
			},
			courseCMD:'',
			createCourseId:0,//新创建的课程id
		};
	},
	created() {
		this.getProduct()
		this.getCourseData();
	},
	mounted () {
		
	},
	methods: {
		getProduct(){
			let that = this;
			this.$axios.get("course/product").then(function (response) {
				if(response.data.ret === 0 ){
					let datas = response.data.data;
					let productOptions = [{value: 0,text: '全部'}]
					for(let k in datas){
						productOptions.push({
							value:datas[k].id,
							text:datas[k].title,
						})
					}
					that.productOptions = productOptions;
				}else{
					that.$message.error('接口异常:'+ response.data.msg)
				}
			})
		},
		getCourseData() {
			let that = this;
			//拼接查询语句
			let url = "course/list?pageSize=" + that.pageConfig.pageSize + "&pageIndex=" + that.pageConfig.pageIndex;
			if(this.search.courseTitle) url += '&courseTitle=' + this.search.courseTitle;

			let timeRange = this.search.timeRange;
			if(timeRange && timeRange.length > 0){
				if(timeRange[0]) url += '&timeStart=' + this.search.timeRange[0]; 
				if(timeRange[1]) url += '&timeEnd=' + this.search.timeRange[1];
			}

			if(this.search.is_public != -1)  url += '&is_public=' + this.search.is_public
			if(this.search.status != -1)  url += '&status=' + this.search.status
			if(this.search.product_id)  url += '&product_id=' + this.search.product_id

			this.$axios.get(url).then(function (response) {
				if(response.data.ret === 0 ){
					that.courseDatas = response.data.data;
					that.pageConfig.pageTotal = response.data.total;
				}else{
					that.$message.error('接口异常:'+ response.data.msg)
				}
			})
		},
		getChapters(id){
			let that = this;
			this.$axios.get("course/detail/" + id).then(function (response) {
				if(response.data.ret === 0 ){
					let datas = response.data.data
					that.courseDetail = datas
					that.courseDetailBefore = JSON.parse(JSON.stringify(datas))
				}else{
					that.$message.error('接口异常:'+ response.data.msg)
				}
			})
		},
		searchCourse(){
			let timeRange = this.search.timeRange;
			if(timeRange && timeRange.length > 0){
				this.search.timeRange[0] = (timeRange[0] + '').length == 13 ? timeRange[0]/1000 : timeRange[0];
				this.search.timeRange[1] = (timeRange[1] + '').length == 13 ? timeRange[1]/1000 : timeRange[0];
			}
			this.getCourseData()
		},
		submitForm(ruleName) {
			this.$refs[ruleName].validate((valid) => {
				if (valid) {
					console.log('submit!');
					this.checkOptions()
				} else {
					return false;
				}
			});
		},
		checkOptions(){
			let newParams = {chapters:[]}
			let a = this.courseDetail;
			let b = this.courseDetailBefore;
			// 检查课程有哪些字段修改过
			if(a.product_title != b.product_title){
				for(let k in this.productOptions){
					if(this.productOptions[k].text == a.product_title){
						newParams['product_id'] = this.productOptions[k].value
						break
					}
				}
			}
			if(a.cover != b.cover) newParams['cover'] = a.cover
			if(a.introduction != b.introduction) newParams['introduction'] = a.introduction
			if(a.title != b.title) newParams['title'] = a.title
			if(a.status != b.status) newParams['status'] = a.status
			if(a.is_public != b.is_public) newParams['is_public'] = a.is_public
			//检查章节有哪些字段修改过
			for(let k in a.chapters){
				let key = a.chapters[k]
				for(let i in b.chapters){
					let item = b.chapters[i]
					//编辑修改
					if(key.id == item.id){
						let params = {}
						if(key.title != item.title) params.title = key.title
						if(key.video_link != item.video_link) params.video_link = key.video_link
						if(key.sort != item.sort) params.sort = key.sort
						if(JSON.stringify(params) != '{}'){
							params.id = key.id
							params.cmd = 'edit'
							newParams.chapters.push(params)
						}
					}
				}
				//新增&删除
				if(key.cmd && key.cmd == 'add') newParams.chapters.push({"cmd":key.cmd,"sort":key.sort,"title":key.title,"video_link":key.video_link,"course_id":key.course_id,"status":1})
				if(key.cmd && key.cmd == 'del') newParams.chapters.push({"id":key.id,"cmd":key.cmd})
			}
			this.setCourse(newParams,a.id)
		},
		setCourse(newParams,id){
			console.log(newParams)
			let that = this;
			let params = { cmd : that.courseCMD }
			if(id) params.id = id
			if(newParams.status >= 0) params.status = newParams.status
			if(newParams.is_public >= 0) params.is_public = newParams.is_public
			if(newParams.cover) params.cover = newParams.cover
			if(newParams.title) params.title = newParams.title
			if(newParams.introduction) params.introduction = newParams.introduction
			if(newParams.product_id) params.product_id = newParams.product_id

			//编辑和删除课程时，id和cmd为必备字段。新增课程时字段长度必须超过2
			let courseLength = 0
			for(let k in params){
				courseLength ++
			}
			let chapters = newParams.chapters
			//操作课程相关
			if(courseLength >= 2){
				that.$axios.post("course/detail", params ).then(function (response) {
					if(response.data.ret == 0 ){
						//新增章节时必须依赖于课程id
						if(response.data.insertId &&  that.courseCMD == 'add'){
							for(let k in chapters){
								let key = chapters[k]
								key.course_id = response.data.insertId
								that.setChapters(key)
							}
						}
					}else{
						that.$message.error('接口异常:'+ response.data.msg)
					}
					that.handleSucess()
				}).catch(function (error) {
					//that.$message.error('请求异常!')
				});
			}
			//操作章节相关
			if(chapters && chapters.length > 0){
				for(let k in chapters){
					let key = chapters[k]
					that.setChapters(key)
				}
			}
		},
		setChapters(parmas){
			let that = this;
			that.$axios.post("course/chapters", parmas ).then(function (response) {
				if(response.data.ret == 0){
					that.handleSucess()
				}else{
					that.$message.error('接口异常:'+ response.data.msg)
				}
			})
		},
		handleSucess(){
			this.$message({message: '操作成功！',type: 'success'})
			this.getCourseData()
			this.dialogVisibleEdit = false
		},
		toggleSelection(rows) {
			if (rows) {
			rows.forEach(row => {
				this.$refs.multipleTable.toggleRowSelection(row);
			});
			} else {
			this.$refs.multipleTable.clearSelection();
			}
		},
		selectionChange(val) {
			this.multipleSelection = val;
		},
		cellClick(row, column, cell, event){
			if(column.label == '课程名称'){
				this.getChapters(row.id)
				this.dialogVisibleShow = true;
				let that = this;
				that.$nextTick(() => {
					//课程简介动态设置高度
					var height= that.$refs.course_desc.offsetHeight || 40;
					that.course_desc_height = height > 40 ? height : 40 +'px';
				})
			}
		},
		openCourseDialog(index, rows) {
			this.courseCMD = 'edit'
			this.getChapters(rows[index].id)
			this.dialogVisibleEdit = true;
		},
		delCourse() {
			this.courseCMD = 'del';
			this.$confirm('此操作将删除所有勾选的课程, 是否继续?', '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning'
			}).then(() => {
				if(this.multipleSelection.length > 0){
					for(let k in this.multipleSelection){
						let key = this.multipleSelection[k]
						this.setCourse({},key.id)
					}
				}
			})
		},
		setCourseStatus(type) {
			this.courseCMD = 'edit'
			let text = type ? '此操作将所勾选的课程发布上线, 是否继续?' :'此操作将所勾选的课程发布下线, 是否继续?'
			this.$confirm(text, '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning'
			}).then(() => {
				if(this.multipleSelection.length > 0){
					for(let k in this.multipleSelection){
						let key = this.multipleSelection[k]
						this.setCourse({"status":type ? 1:0},key.id)
					}
				}
			})
		},
		addCourse(index, rows) {
			let newDetail = {
				product_title:'',
				title:'',
				introduction:'',
				cover:'',
				status:0,
				is_public:0,
				chapters:[]
			}
			this.courseDetail = newDetail
			this.courseDetailBefore = JSON.parse(JSON.stringify(newDetail))
			this.dialogVisibleEdit = true;
			this.courseCMD = 'add'
		},
		addChapter(){
			this.setChapterSort()
			let len = this.courseDetail.chapters.length + 1
			let params = {title:'',video_link:'',cmd:'add',sort:len,course_id:this.courseDetail.id,status:1}
			if(this.courseCMD == 'add') params.id = len
			this.courseDetail.chapters.push(params)
		},
		removeChapter(item) {
			let chapters = this.courseDetail.chapters
			for(let k in chapters){
				if(chapters[k].id == item.id){
					chapters[k].status = 0
					chapters[k].cmd = 'del'
					break
				}
			}
			this.setChapterSort()
      	},
		//重新编辑章节排序字段sort
		setChapterSort(){
			let chapters = this.courseDetail.chapters;
			if(chapters.length > 0){
				for(let k in chapters){
					this.courseDetail.chapters[k].sort = parseInt(k)+1
				}
			}
		},
		moveChapter(item,type) {
			//对调前后章节的tittle、video_link
			let chapters = this.courseDetail.chapters
			for(let k in chapters){
				let key = chapters[k];
				if(key.id == item.id){
					let side = 0
					if(type == 'down' && k != chapters.length){
						side = parseInt(k)+1
					}
					if(type == 'up' && k != 0){
						side = parseInt(k)-1
					}
					let curKey = JSON.parse(JSON.stringify(key))
					// this.courseDetail.chapters[k].sort =  chapters[side].sort
					this.courseDetail.chapters[k].title =  chapters[side].title
					this.courseDetail.chapters[k].video_link =  chapters[side].video_link

					// this.courseDetail.chapters[side].sort = curKey.sort
					this.courseDetail.chapters[side].title = curKey.title
					this.courseDetail.chapters[side].video_link = curKey.video_link
					break
				}
			}
      	},
		handleSizeChange(val) {
			this.pageConfig.pageIndex = 1;
			this.pageConfig.pageSize = val;
			this.getCourseData()
		},
		handleCurrentChange(val) {
			this.pageConfig.pageIndex = val;
			this.getCourseData()
		},
		resetForm(formName) {
			this.$refs[formName].resetFields();
		},
		filterProductTitle(value, row) {
			return row.product_title === value;
		},
		filterStatus(value, row) {
			return row.status === value;
		},
		filterPublic(value, row) {
			return row.is_public === value;
		},
		formatDateTime(time) {
			if((time + '').length == 10) time = time*1000;
			let date = new Date(time), y = date.getFullYear(),m = date.getMonth() + 1,d = date.getDate(),
				h = date.getHours(),minute = date.getMinutes(),second = date.getSeconds()
			m = m < 10 ? ('0' + m) : m;
			d = d < 10 ? ('0' + d) : d;
			h = h < 10 ? ('0' + h) : h;
			minute = minute < 10 ? ('0' + minute) : minute;
			second = second < 10 ? ('0' + second) : second;
			return y + '-' + m + '-' + d+' '+h+':'+minute+':'+second;
		},
	},
}
</script>
<style scoped>
	.clear{
		overflow: hidden;
		clear: both;
	}
	.course{
		width: 1180px;
		padding: 0 20px;
		font-size: 14px;
		border: 1px solid #eee;
	}
	.pd20{
		padding: 20px 0;
	}
	.search .search_item,.search .search_btn{
		float: left;
		margin-right: 26px;
		margin-bottom: 20px;
	}
	.search .el-input{
		font-size: 14px;
		width:200px;
		display: inline-block;
	}
	.tools button{
		float: left;
	}
	.dialog{
		text-align: left;
		color: #606266;
		font-size: 14px;
	}
	.dialog .dialog_section{
		position: relative;
		width: 800px;
		margin: 0 auto 50px auto;
	}
	.dialog .dialog_section h3{
		margin: 0;
		height: 20px;
		margin-bottom: 10px;
	}
	.dialog .dialog_section .dialog_list{
		padding: 10px;
		border: 1px solid #eaeefb;
	}
	.dialog .dialog_section .section_item{
		height: 40px;
		border-bottom: 2px solid #fff;
	}
	.dialog .dialog_section .section_item > div{
		vertical-align: top;
		height: 40px;
		line-height: 40px;
		display: inline-block;
	}
	.dialog .dialog_section .section_item .section_item_name{
		width:80px;
		text-align: center;
		background-color: #eff8ff;
	}
	.dialog .dialog_section .section_item .section_item_content{
		width: 680px;
		overflow: hidden;
	}
	.dialog .dialog_section .section_item_desc{
		height: auto;
	}
	.dialog .dialog_section .section_item_desc .section_item_content{
		line-height: 20px;
		height: auto;
	}
	.editDialog .dialog_section .section_item > div{
		vertical-align: middle;
	}
	.editDialog .input{
		width: 800px!important
	}
	.editDialog .editDialogBtn{
		position: absolute;
		top: -8px;
		right: 0;
	}
</style>



